<template>
  <span>
    <button class="button is-small" @click="modalVisible = true">
      <span v-if="selectedTemplate.name">
        {{selectedTemplate.name}}
      </span>
      <i v-else>
        No template selected
      </i>
    </button>
    <b-modal :active.sync="modalVisible" has-modal-card :canCancel="true">
      <div class="card" style="padding: 10px; min-width: 800px; min-height: 500px;">
        <h1 style="font-size: 18pt; font-weight: bold;">Choose a template for your website</h1>
        <div v-for="t in templates">
          <button class="button" @click="selectTemplate(t)">
            {{t.name}}
          </button>
        </div>
      </div>
    </b-modal>
  </span>
</template>
<script>

export default {
  data() {
    return {
      templates: [{
        "repoUsername": "gwenaelp",
        "repoName": "vue-git-cms-template-simple",
        "name": "Simple"
      }],
      selectedTemplate: {},
      modalVisible: false
    };
  },
  methods: {
    selectTemplate(t) {
      this.selectedTemplate = t;
      this.$emit('onChange', t);
      this.modalVisible = false;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



