<template>
  <b-modal :active.sync="modalVisible" has-modal-card :canCancel="false">
    <div class="card" style="padding: 10px">
      <b-tabs v-model="activeTab" class="loginTabs">
        <b-tab-item label="">
          <h1 style="font-size: 18pt; font-weight: bold;">Login</h1>
          <b-field label="Name">
            <b-input v-model="loginUserName" name="username" placeholder="GitHub user name"></b-input>
          </b-field>
          <b-field label="Password" >
            <b-input type="password" v-model="loginPassword" name="password" placeholder="GitHub password"></b-input>
          </b-field>
          <b-field label="Site Name" >
            <b-input v-model="loginSiteName" name="sitename" placeholder="GitHub repository name"></b-input>
          </b-field>
          <button class="button is-medium is-primary loginButton right" @click="login">
              Login
          </button>
          <div style="clear: both;"></div>
          <div>Not having a website yet?</div>
          <a @click="activeTab = 1" class="registerButton">Create a new website →</a>
          <div style="clear: both;"></div>
        </b-tab-item>
        <b-tab-item label="">
          <a @click="activeTab = 0">← Back to login</a>
          <h1 style="font-size: 18pt; font-weight: bold;">Create a new website</h1>
          (Work in progress)
          <b-field label="Name">
            <b-input v-model="registerUserName"  name="username"></b-input>
          </b-field>
          <b-field label="Password" >
            <b-input type="password" v-model="registerPassword" name="password"></b-input>
          </b-field>
          <b-field label="Site Name" >
            <b-input v-model="registerSiteName" name="sitename"></b-input>
          </b-field>
          <b-field label="Template" >
            <TemplateSelector @onChange="changeRegisterTemplate" />
          </b-field>
          <button class="button is-medium is-primary loginButton right" @click="create">
              Create
          </button>
          <div style="clear: both;"></div>
        </b-tab-item>
      </b-tabs>
    </div>
  </b-modal>
</template>
<script>
import GitHub from 'github-api';
import { mapState } from 'vuex';
import ghApi from '@/GithubApi';
import TemplateSelector from '@/components/TemplateSelector';

console.log('GH', ghApi);
export default {
  props: ['visible'],

  data() {
    return {
      activeTab: 0,
      loginUserName: '',
      loginPassword: '',
      loginSiteName: '',
      isLoginModalActive: true,
      registerUserName: '',
      registerPassword: '',
      registerSiteName: '',
      registerTemplate: {},
    };
  },
  methods: {
    create() {
      alert('startCreate');
      this.$store.github = new GitHub({
        username: this.loginUserName,
        password: this.loginPassword,
        auth: 'basic'
      });

      var repo = this.$store.github.getRepo(this.registerTemplate.repoUsername, this.registerTemplate.repoName);

      repo.fork().then((arg1, arg2) => {
        console.log('forked', arg1, arg2);
      });

    },

    changeRegisterTemplate(template) {
      this.registerTemplate = template;
    },

    login() {
      this.$store.github = new GitHub({
        username: this.loginUserName,
        password: this.loginPassword,
        auth: 'basic'
      });

      this.$store.repository = this.$store.github.getRepo(this.loginUserName, this.loginSiteName);

      ghApi.init(this.$store.github, this.$store.repository);
      ghApi.setBranch('gh-pages').then(() => {
        this.$store.dispatch('authUser');
      });
    },

  },
  computed: mapState({
    modalVisible: state => !state.authenticated,
  }),

  components: {
    TemplateSelector,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.loginButton {
  margin-bottom: 20px;
}

.right {
  float: right;
}

</style>
<style>
.loginTabs nav {
  display: none;
}
</style>



