<template>
  <div style="width: 100%">
    <div v-if="fragmentContent">
      <vue-form-generator :schema="fragment.schema" :model="fragmentContent"></vue-form-generator>
    </div>
    <div class="lds-hourglass" v-else></div>
  </div>
</template>
<script>
import EditorGalleryImage from '@/components/EditorGalleryImage';
import { setFragmentContent, fragmentContent as getFragmentContent, getGithubFile} from '@/utils';

import { mapState } from 'vuex';

import { writeGitFile, fragmentContent,  } from '@/utils';

export default {
  props: ["page", "fragment"],

  data() {
    getGithubFile(this.$store, this.fragment.fragment, (responseText) => {
      responseText = atob(responseText.content);
      console.log(responseText);
      try {
        content = JSON.parse(responseText);
        setFragmentContent(this.$store, this.fragment.fragment, JSON.stringify(content, null, 2));
        this.fragmentContent = content;
      } catch(e) {
        var content = {};
        this.fragmentContent = content;
        setFragmentContent(this.$store, this.fragment.fragment, JSON.stringify(this.fragmentContent, null, 2));
      }
    });

    return {
      selectionRange: undefined,
      fragmentContent: '',
    };
  },

  methods: {
  },
  watch: {
    fragment(newValue) {
      if(newValue) {
        getGithubFile(this.$store, this.fragment.fragment, (responseText) => {
          responseText = atob(responseText.content);
          try {
            var content = JSON.parse(responseText);
            setFragmentContent(this.$store, this.fragment.fragment, JSON.stringify(content, null, 2));
            this.fragmentContent = content;
          } catch(e) {
            var content = {};
            setFragmentContent(this.$store, this.fragment.fragment, JSON.stringify(content, null, 2));
            this.$emit('onEdit');
            this.fragmentContent = content;
          }
        });
      }
    },
    fragmentContent: {
      handler(newValue) {
        setFragmentContent(this.$store, this.fragment.fragment, JSON.stringify(newValue, null, 2));
      },
      deep: true
    }
  },

  components: {
    EditorGalleryImage,
  },

  computed: {
    config: {
      get() {return this.$store.state.config; },
      set(value) {},
    },
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



